<template>
    <nav>
      <ul>
        <li v-for="rota in rotas" :key="rota">
            <router-link :to="rota.path ? rota.path:'/'">
                {{rota.titulo}}
            </router-link>
        </li>
        
      </ul>
    </nav>
  
</template>

<script>
export default {
    
    props: {
        /**validando elemento rotas */
        rotas:{
            type: Array,
            required:true
        }
    }
    
}
</script>

<style>

</style>



